import Typography from '@material-ui/core/Typography';
import React from 'react';
import { LinearProgress } from '@material-ui/core';

export default class AreaExpansionPanelDetail extends React.Component < {area: string} > {
    
    state: {
        ready: false
    }

    render () {

        if (!this.state.ready) {
            return <LinearProgress />
          }
      
        return <p>{this.props.area}</p>
    }
}